<template>
	<li>
		<i :class="iconClass" class="sidebar_nav_icon fa fa-fw" aria-hidden="true"></i>
		<a class="nostyle" href="#" v-scroll-to="{
				el: targetId,
				duration: 1000,
				easing: 'ease-in-out'
		}">
			<span class="sidebar_item_name">{{ name }}</span>
		</a>
	</li>
</template>

<script>
	import VueScrollTo from 'vue-scrollto';

	export default {
		name: "SidebarItem",
		props: [
			"name",
			"iconClass",
			"targetId"
		],
		directives: {
			scrollTo: VueScrollTo
		},
		data () {
			return {}
		}
	}
</script>

<style lang="scss" scoped>
	$sidebar_item_size: 50px;

	li {
		width: $sidebar_item_size;
		height: $sidebar_item_size;
		line-height: $sidebar_item_size;
		&:hover {
			& a {
				left: 0px;
			}
		}
		& i.sidebar_nav_icon {
			position: absolute;
			width: $sidebar_item_size;
			height: $sidebar_item_size;
			line-height: $sidebar_item_size;
			font-size: 25px;
		}
		& a {
			//line-height: $sidebar_item_size;
			display: block;
			position: absolute;
			padding: 0px 25px 0px 25px;
			left: -400px;
			height: $sidebar_item_size;
			box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
			background-color: white;
			white-space: nowrap;
			//text-align: left;
			font-family: 'Barlow Condensed', sans-serif;
			font-size: 1.5em;
			text-decoration: none;
			transition: all 0.1s ease-in;
			& .sidebar_item_name {
				display: inline-block;
				max-width: 300px;
				text-overflow: ellipsis;
				overflow: hidden;
			}
			//&::after {
			//	content: '\f105';
			//	font-family: FontAwesome;
			//	margin-left: 10px;
			//}
		}
	}
</style>


