<template>
	<div>
		<sidebar/>
		<title-section/>
		<experiences-section/>
		<projects-section/>
		<!-- <contact-me-section/> -->
	</div>
</template>

<script>
	import Sidebar from './sidebar/Sidebar'
	import TitleSection from './landingPageSections/TitleSection'
	import ExperiencesSection from './landingPageSections/ExperiencesSection'
	import ProjectsSection from './landingPageSections/ProjectsSection'
	//import ContactMeSection from './landingPageSections/ContactMeSection'
	export default {
		name: "LandingPage",
		components: {
			Sidebar,
			TitleSection,
			ExperiencesSection,
			ProjectsSection,
			//ContactMeSection
		}
	}
</script>

<style lang="scss">
	hr {
		border:none;
		border-top: 1px white solid;
		height: 1px;
	}
	a.nostyle:link {
			text-decoration: inherit;
			color: inherit;
	}
	.goto_next {
		outline: 0;
		border-radius: 50%;
		width: 75px;
		height: 75px;
		color: rgba(255, 255, 255, 0.8);
		background-color: rgba(0, 0, 0, 0.4);
		bottom: 0px;
		margin-bottom: 50px;
		display: flex;
		position: absolute;
		justify-content: center;
		align-items: center;
		-moz-transition: all 0.2s ease;
		-webkit-transition: all 0.2s ease;
		-o-transition: all 0.2s ease;
		transition: all 0.2s ease;
		&:hover {
			color: cyan;
			background-color: rgba(0, 0, 0, 0.8);
			margin-bottom: 40px;
			-moz-transition: all 0.1s ease;
			-webkit-transition: all 0.1s ease;
			-o-transition: all 0.1s ease;
			transition: all 0.1s ease;
		}
	}
	.section {
		display: flex;
		justify-content: center;
		& .section_content {
			width: 66%;
		}
	}
	@media (min-width: 300px) and (max-width: 500px) {
		.sidebar {
			visibility: hidden;
		}
		.section {
			display: flex;
			justify-content: center;
			& .section_content {
				width: 100%;
			}
		}
	}
	@media (min-width: 500px) and (max-width: 732px) {
		.goto_next {
			right: 0px;
			margin-right: 50px;
		}
	}
</style>


