<template>
	<div class="sidebar">
		<a href="/" class="nostyle logo">
			<img src="../../assets/block_255.png" alt="">
		</a>
		<ul class="sidebarItemList">
				<sidebar-item
					v-bind:key="item"
					v-for="item in items"
					:name="item.name"
					:target-id="item.targetId"
					:icon-class="item.iconClass"
				/>
		</ul>
		<a href="mailto:daniel.junior.dube@gmail.com" class="nostyle mail_to">
			<i class="fa fa-fw fa-envelope" aria-hidden="true"></i>
		</a>
	</div>
</template>

<script>
	import SidebarItem from './SidebarItem'
	export default {
		name: "Sidebar",
		components: {
			SidebarItem,
		},
		data () {
			return {
				items: [
					{
						name: "Landing",
						targetId: "#title_section",
						iconClass: "fa-user-circle"
					},
					{
						name: "Experiences",
						targetId: "#experiences_section",
						iconClass: "fa-briefcase"
					},
					/* {
						name: "Projects",
						targetId: "#projects_section",
						iconClass: "fa-terminal"
					},
					{
						name: "Contacts",
						targetId: "#contacts_section",
						iconClass: "fa-envelope"
					} */
				]
			}
		}
	}
</script>

<style lang="scss" scoped>
	.sidebar {
		z-index: 9999;
		position: fixed;
		width: 50px;
		height: 100%;
		background-color: white;
		color: black;
		box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
		& .logo img {
			margin-top: 5px;
			padding: 5px;
			width: 40px;
			height: 40px;
		}
		& .sidebarItemList {
			padding: 0px;
			margin: 0px;
			margin-top: 5px;
			list-style: none;
		}
		& a.mail_to {
			display: flex;
			justify-content: center;
			align-items: center;
			bottom: 5px;
			position: absolute;
			line-height: 50px;
			padding: 0px;
			margin: 0px;
			display: block;
			width: 50px;
			height: 50px;
			&:hover {
				& i {
					color: cyan;
					background-color: rgba(0, 0, 0, 1);
					border-radius: 3px;
					-moz-transition: all 0.1s ease-in;
					-webkit-transition: all 0.1s ease-in;
					-o-transition: all 0.1s ease-in;
					transition: all 0.1s ease-in;
				}
			}
			& i {
				margin: 5px;
				position: absolute;
				width: 40px;
				height: 40px;
				line-height: 40px;
				font-size: 25px;
				-moz-transition: all 0.2s ease-in;
				-webkit-transition: all 0.2s ease-in;
				-o-transition: all 0.2s ease-in;
				transition: all 0.2s ease-in;
			}
		}
	}
</style>


