<template>
	<section id="title_section" class="section">
		<div class="section_content">
			<div id="title_container">
				<span class="my_name no_text_select">
					<b>Daniel-Junior Dub</b>
				</span>
				<span class="under_my_name no_text_select" >
					Portfolio
				</span>
				<div class="title_social_media_links">
					<hr>
					<a href="https://twitter.com/HEYIMFUZZ">
						<i class="twitter_icon fa fa-twitter" aria-hidden="true"></i>
					</a>
					<a href="">
						<i class="github_icon fa fa-github" aria-hidden="true"></i>
					</a>
					<a href="https://www.facebook.com/junior.dube.5">
						<i class="facebook_icon fa fa-facebook-official" aria-hidden="true"></i>
					</a>
					<a href="https://www.linkedin.com/in/daniel-junior-dub-418a3381">
						<i class="linkedin_icon fa fa-linkedin " aria-hidden="true"></i>
					</a>
				</div>
				<!-- TODO: Add "employment status" under "social media" -->
				<!-- TODO: Add "learn more about me" at bottom, centered -->
			</div>
		</div>
		<a href="#" class="goto_next" v-scroll-to="{
				el: '#experiences_section',
				duration: 1000,
				easing: 'ease-in-out'
		}">
			<span>
				<i class="fa fa-3x fa-angle-double-down" aria-hidden="true"></i>
			</span>
		</a>
	</section>
</template>

<script>
	import VueScrollTo from 'vue-scrollto';

	export default {
		name: "LandingPageTitleSection",
		directives: {
			scrollTo: VueScrollTo
		}
	}
</script>

<style lang="scss" scoped>
	.section {
		min-height: 100vh;
		align-items: center;
		flex-direction: column;
		background-image:url(../../assets/me.png);
		background-position:right bottom;
		background-repeat: no-repeat;
		background-size: auto 100%;
		& .section_content {
			& #title_container {
				padding: 50px;
				font-family: 'Barlow Condensed', sans-serif;
				display: inline-flex;
				flex-direction: column;
				.my_name {
					//text-decoration: underline;
					text-transform: uppercase;
					text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.8);
					//font-weight: 800;
					font-size: 3.5em;
				}
				.under_my_name {
					text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.8);
					font-size: 2.5em;
				}
				.title_social_media_links {
					margin-top: 10px;
					& i {
						background-color: rgba(0, 0, 0, 0.8);
						border-radius: 2px;
						padding: 10px;
						font-size: 1.5em;
						-moz-transition: all 0.2s ease-in;
						-webkit-transition: all 0.2s ease-in;
						-o-transition: all 0.2s ease-in;
						transition: all 0.2s ease-in;
						&:hover {
							-moz-transition: all 0.1s ease-in;
							-webkit-transition: all 0.1s ease-in;
							-o-transition: all 0.1s ease-in;
							transition: all 0.1s ease-in;
							background-color: white;
						}
						&.linkedin_icon {
							color: #0077b5;
						}
						&.twitter_icon {
							color: #55acee;
						}
						&.github_icon {
							color: white;
							&:hover {
								color: black;
							}
						}
						&.facebook_icon {
							color: #3B5998;
						}
					}
				}
			}
		}
	}
</style>


